import Vue from 'vue'
import Router from 'vue-router'

import Home from './components/Home.vue' 
import about from './components/about.vue' 
import Slider from './components/slider.vue' 

Vue.use(Router)

export default new Router({
  mode: 'hash',//  mode: 'history',||  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,      
      meta: {
        title: 'Home Page', 
      }
    },
    {
      path: '/About',
      name: 'about',
      component: about,      
      meta: {
        title: 'About Page', 
      }
    },
    {
      path: '/Slider',
      name: 'Slider',
      component: Slider,      
      meta: {
        title: 'Slider Page', 
      }
    } 
  ]
})
